.headerWrap{
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #ddd;
    .header-area{
      position: relative;
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
        padding: 0 30px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100px;
       .logo{
          a{
             img{
                width: 150px;
            }
          }
       }
       .nav{
         .signUpandlogin{
            position: absolute;
            top: 5px;
            right: 30px;
            li{
               a{
                  font-size: 14px;
                  position: relative;
                  &::after{
                     content: "";
                     width: 1px;
                     height: 10px;
                     background: #323232;
                     position: absolute;
                     top:15px;
                     right: 0;
                  }
                  
                  
               }&:last-child a:after{
                  width: 0;
               }
            }
         }
          ul{
            display: flex;
            list-style: none;
             li{
                a{
                    text-decoration: none;
                    color: #3e3e3e;
                    padding: 10px 20px;
                    &:hover{
                        color: #3acbb0;
                    }
                }
             }
          }
       }
    }
 }