.bestWrap {
    padding: 50px 30px;

    h2 {
        text-align: center;
    }

    >p {
        font-weight: 500;
        text-align: center;
        color: #bbb;
        font-size: 14px;
    }

    .best {
        width: 100%;
        max-width: 1280px;
        margin: 30px auto;
        height: 640px;

        >ul {
            width: 100%;
            height: 100%;
            display: flex;
            >li {
                width: 50%;
                height: 100%;
                >a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    background: #f7f7f7 url(../img/best01.jpg);
                    >.bestsp {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        .bestTextBox {
                            width: 50%;
                            b {
                                font-size: 25px;
                                color: #000;
                            }
                            p {
                                padding-top: 30px;
                                font-size: 14px;
                                color: #555;
                                position: relative;
                                &::before{
                                    content: "";
                                    width: 50px;
                                    height: 1px;
                                    background: #555;
                                    position: absolute;
                                    top: 15px;
                                    left: 0;
                                }
                            }

                            .sp-empty {
                                border: 1px solid #333;
                                background: none;
                                color: #555;
                                padding: 5px 10px;
                                position: relative;
                                top:20px;
                                border-radius: 3px;
                                z-index: 2;
                                display: inline-block;  
                                &::after{
                                    content: "More Btn";
                                    width: 0;
                                    height: 100%;
                                    overflow: hidden;
                                    background: #555;
                                    padding: 5px 0px;
                                    text-align: center;
                                    box-sizing: border-box;
                                    color: #fff;
                                    position: absolute;
                                    top:0;
                                    left: 0;
                                    z-index: 1;
                                    transition: .3s;
                                }
                                &:hover::after{
                                    width: 100%;
                                }                              
                            }
                        }
                    }
                }

                >.bestbox {
                    width: 100%;
                    >ul {
                        display: flex;
                        flex-wrap: wrap;
                        >li {
                            width: 50%;
                            height: 320px;
                            a {
                                display: block;
                                width: 100%;
                                height: 100%;
                                background: #fbfbfb;
                                position: relative;
                                .aWrap {
                                    padding: 80px 20px;
                                    width: 100%;
                                    height: 150px;
                                    b {
                                        font-size: 22px;
                                        color: #333;
                                    }

                                    p {
                                        font-size: 14px;
                                        color: #555;
                                        padding-top: 30px;
                                        position: relative;
                                        letter-spacing: -1px;
                                        &::before{
                                            content: "";
                                            position: absolute;
                                            top:15px;
                                            left: 0;
                                            width: 50px;
                                            height: 1px;
                                            background: #555;
                                        }
                                    }
                                    >span{
                                        width: 100%;
                                        height: 100%;
                                        position: absolute;
                                        background: rgba(0,0,0,.2);
                                        top:0;
                                        left: 0;
                                        opacity: 0;
                                        transition: .5s;
                                        >i{
                                            display: flex;
                                            justify-content: center;
                                            align-items: center;
                                            width: 100%;
                                            height: 100%;
                                            button{
                                                border: 1px solid #fff;
                                                color: #fff;
                                                background: none;
                                                padding: 10px 20px;
                                            }
                                        }
                                    }
                                    &:hover span{
                                        opacity: 1;
                                    }
                                }
                            }
                            &:nth-child(2) a{
                                width: 100%;
                                height: 100%;
                                background: url(../img/best02.jpg) no-repeat top left/100%;
                            }
                            &:nth-child(3) a{
                                width: 100%;
                                height: 100%;
                                background: url(../img/bset03.jpg) no-repeat top left/100%;
                            }
                        }
                        
                    }
                }
            }
        }
    }
}